import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Content from '@/components/content/Content';
import Episode from '@/components/content/Episode';
import Search from '@/components/Search';

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { name: 'home',    component: Home,    path: '/' },
    { name: 'content', component: Content, path: '/anime/:content_id',
      children: [
        { name: 'episode', component: Episode, path: 'episode/:episode' },
      ]
    },
    { name: 'search',  component: Search,  path: '/search/:search_query'},
  ]
})



